/*import brush.brush.png from './assets/brush.png'*/


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box ;
}

html{
  background: #536872;
}

.App {
  width: 100vw;
  height: 100hw;
  max-height: 100vw;
  text-align: center;
  background-color: #E5E5E5;
  font-family: Arial;
  color: #536872;
  font-size: 15px;
}


.toolbar, .setting-bar{
background-color:#D9D9D9;
display: flex;
flex-wrap: wrap;
align-items: center;
position: absolute;
width: 100%;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.toolbar{
  height: 40px;
  z-index: 999;
background-color:#71A8AE;
}

.setting-bar{

}

.toolbar__btn {
  width: 25px;
height: 25px;
border: none;
outline: none;
cursor: pointer;
background-size: cover;
margin-left: 16px;
}

label{
  margin:10px;
}

.brush{
  background: url("assets/img/brush7.png") no-repeat center center;
    background-size: contain;
  margin-left: 28px ;
}

.rect{
  background: url("assets/img/rect2.png") no-repeat center center;
    background-size: contain;
}

.circle{
  background: url("assets/img/circle2.png") no-repeat center center;
    background-size: contain;
}

.eraser{
  background: url("assets/img/eraser2.png")   no-repeat center center;
    background-size: contain;
}

.eraser2{
  background: url("assets/img/eraser3.png")   no-repeat center center;
    background-size: contain;
}

.line{
  background: url("assets/img/line2.png") no-repeat center center;
}

.changeColor{
  margin-left: 19px;
  border:2px solid #B7B7B7;
}

input{
  border:2px solid gray;
  width:28px;
  border-radius: 3px;
}

#canvas-width-change{
  margin-left:50px;
width: 150px;
}

.undo{
  background: url("assets/img/undo2.png") no-repeat center center;
  margin-left: auto ;
    background-size: contain;
}

.redo{
  background: url("assets/img/redo2.png") no-repeat center center;
    background-size: contain;
}

.save{
  background: url("assets/img/save3.png") no-repeat center center;
  margin-right: 28px ;
    background-size: contain;
}

.setting-bar{
  top: 40px;
}

.canvas {
  overflow: auto;
   height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #536872;
  margin: 0 auto;
  padding:10px;
  padding-top: 90px;
  border:1px solid gray;
}

canvas{
background-color: white;
}